  // Функция для установки текущей даты
  function setCurrentDate() {
    const today = new Date(); // Получаем текущую дату
    const dd = String(today.getDate()).padStart(2, '0'); // День
    const mm = String(today.getMonth() + 1).padStart(2, '0'); // Месяц (нумерация с 0)
    const yyyy = today.getFullYear(); // Год
    
    // Форматируем дату в формате YYYY-MM-DD
    const formattedDate = `${yyyy}-${mm}-${dd}`;
    
    // Устанавливаем значение в input
    document.getElementById('date').value = formattedDate;
}

// Вызываем функцию при загрузке страницы
window.onload = setCurrentDate;


const obratnoButton = document.querySelector('.buttonZuruck');
const oneWayButton = document.querySelector('input[value="oneWay"]'); // Кнопка "В одну сторону"
const blockSec = document.querySelector('.zuruck');

// Функция для показа блока
function showBlock() {
    blockSec.style.display = 'block';
}

// Функция для скрытия блока
function hideBlock() {
    blockSec.style.display = 'none';
}

// Обработчики событий для кнопок
obratnoButton.addEventListener('click', showBlock);
oneWayButton.addEventListener('click', hideBlock);





